        { id: 6, title: '3D Space Ship', icon: 'videogame_asset', img: 'https://picsum.photos/500/300?image=75', img_lazy: 'https://picsum.photos/10/6?image=65', text: 'Description courte du projet', description: 'Description plus longue du projet' }
      ]
    }
  },
  watch: {
    dialog: function () {
      if (!this.dialog) {
        for (let i = 0; i < 9; i++) {
          document.getElementsByClassName('hover-menu')[i].classList.remove('opened')
          document.getElementsByClassName('open-viewer')[i].classList.remove('opened')
        }
      }
    }
  },
  methods: {
    openDialog (n, title) {
      this.titre_viewer = title
      this.dialog = true
      document.getElementsByClassName('hover-menu')[n - 1].classList.add('opened')
      document.getElementsByClassName('open-viewer')[n - 1].classList.add('opened')
      this.changeHeight()
    },
    changeHeight () {
      if (window.innerWidth < 960) {
        document.getElementById('main-height').style.height = (window.innerWidth * 0.4) * (4 / 3) + 'px'
      } else {
        document.getElementById('main-height').style.height = (window.innerWidth * 0.4) * (9 / 16) + 'px'
      }
    }
  },
  mounted () {
    this.changeHeight()
    window.addEventListener('resize', this.changeHeight)
  }
}
</script>

<style scoped>
  .hover-theme {
