    transform-origin: center;
  }
  .open-viewer2 {
    position: relative;
    cursor: pointer;
  }
  .hover-menu {
    position: absolute;
    top: 100%;
    z-index: 1;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,0.7);
    -webkit-transition: all 0.2s;
    -moz-transition: all 0.2s;
    -ms-transition: all 0.2s;
    -o-transition: all 0.2s;
    transition: all 0.2s;
  }
  .open-viewer {
    -webkit-transition: all 0.2s;
    -moz-transition: all 0.2s;
    -ms-transition: all 0.2s;
    -o-transition: all 0.2s;
    transition: all 0.2s;
  }
  .open-viewer2:hover .open-viewer, .open-viewer.opened {
    transform-origin: center;
    transform: scale(1.5, 1.5);
  }
  .open-viewer2:hover > .hover-menu, .hover-menu.opened {
    top: 0
  }
  .border-r {
    border-radius: 50%;
    padding: 25px;
    border: 3px solid #009688;
  }
  .border-t {
    padding: 15px;
    border: 2px solid #009688;
  }
  .border-l {
    border-left: 1px solid #aaa;
  }
  .border-b {
    border-bottom: 1px solid #aaa;
  }
  @media screen and (min-width: 1264px){
    .text-pad {
